import { useQuery } from 'react-query';
import { matchPath, useLocation } from 'react-router-dom';
import styled from 'styled-components';
import { getOnTheAirTV, getPopularTV, getTopRatedTV, IGetTVResult } from '../api';
import Banner from '../Components/Common/Banner';
import Details from '../Components/Movie/Details';
import Slider from '../Components/Slider/Slider';

const Wrapper = styled.div`
    background-color: black;
    padding-bottom: 200px;
`;

const Loader = styled.div`
    height: 20vh;
    display: flex;
    justify-content: center;
    align-items: center;
`;

function TV(): JSX.Element {
    const location = useLocation();
    const tvMatch = matchPath('/tv/:category/:tvId', location.pathname);
    const tvHomeMatch = matchPath('/tv', location.pathname);


    const { data: topRatedTV, isLoading: isTopRatedLoading } = useQuery<IGetTVResult>(
        ['tv', 'topRated'],
        getTopRatedTV,
        { enabled: (tvMatch !== null) || (tvHomeMatch !== null) }
    );

    const { data: popularTV, isLoading: isPopularLoading } = useQuery<IGetTVResult>(
        ['tv', 'popular'],
        getPopularTV,
        { enabled: (tvMatch !== null) || (tvHomeMatch !== null) }
    );

    const { data: onTheAirTV, isLoading: isOnTheAirLoading } = useQuery<IGetTVResult>(
        ['tv', 'nowPlaying'],
        getOnTheAirTV,
        { enabled: (tvMatch !== null) || (tvHomeMatch !== null) }
    );

    const firstTV = onTheAirTV?.results[0];

    const isLoading = isTopRatedLoading || isPopularLoading || isOnTheAirLoading

    const clickedTv = () => {
        if (!tvMatch?.params.tvId) {
            return;
        }

        if (tvMatch?.params.category === "On The Air") {
            return onTheAirTV?.results.find(tv => tv.id + "" === tvMatch.params.tvId);
        }

        if (tvMatch?.params.category === "Popular") {
            return popularTV?.results.find(tv => tv.id + "" === tvMatch.params.tvId);
        }

        if (tvMatch?.params.category === "Top Rated") {
            return topRatedTV?.results.find(tv => tv.id + "" === tvMatch.params.tvId);
        }
    }

    const tv = clickedTv();


    return (
        <Wrapper>
            {isLoading ? (
                <Loader>Loading...</Loader>
            ) : (
                <>
                    <Banner imagePath={firstTV?.backdrop_path!} title={firstTV?.name!} overview={firstTV?.overview!} />
                    <Slider menu="tv" movies={undefined} tvs={onTheAirTV} category="On The Air"></Slider>
                    <Slider menu="tv" movies={undefined} tvs={popularTV} category="Popular"></Slider>
                    <Slider menu="tv" movies={undefined} tvs={topRatedTV} category="Top Rated"></Slider>
                    {tvMatch && <Details menu="tv" title={tv?.name!} imagePath={tv?.backdrop_path!} overview={tv?.overview!} />}
                </>
            )}
        </Wrapper>
    );
}

export default TV;