const API_KEY = 'f4049c7abedc8471730a0b33400686f3';
const BASE_URL = 'https://api.themoviedb.org/3';

export interface IMovie {
    id: number;
    backdrop_path: string;
    overview: string;
    poster_path: string;
    title: string;
}

export interface IGetMovieResult {
    dates: {
        minimum: string;
        maximum: string;
    };
    page: number;
    results: IMovie[];
    total_pages: number;
    total_results: number;
}

export interface ITV {
    id: number;
    backdrop_path: string;
    overview: string;
    poster_path: string;
    name: string;
}

export interface IGetTVResult {
    page: number;
    results: ITV[];
    total_pages: number;
    total_results: number;
}

export const request = (url: string, options: RequestInit = {}) => {
    const result = fetch(url, options)
        .then((response) => {
            if (!response.ok) {
                throw response.statusText;
            }
            return response.json();
        })
        .catch((error) => {
            alert(error.message);
        });

    return result;
};

export function getNowPayingMovies() {
    return request(`${BASE_URL}/movie/now_playing?api_key=${API_KEY}`);
}

export function getTopRatedMovies() {
    return request(`${BASE_URL}/movie/top_rated?api_key=${API_KEY}`);
}

export function getUpComingMovies() {
    return request(`${BASE_URL}/movie/upcoming?api_key=${API_KEY}`);
}


export function getLatestMovies() {
    return request(`${BASE_URL}/movie/latest?api_key=${API_KEY}`);
}



export function getOnTheAirTV() {
    return request(`${BASE_URL}/tv/on_the_air?api_key=${API_KEY}`);
}

export function getTopRatedTV() {
    return request(`${BASE_URL}/tv/top_rated?api_key=${API_KEY}`);
}

export function getPopularTV() {
    return request(`${BASE_URL}/tv/popular?api_key=${API_KEY}`);
}

