import { useQuery } from 'react-query';
import { matchPath, useLocation } from 'react-router-dom';
import styled from 'styled-components';
import { getNowPayingMovies, getLatestMovies, getUpComingMovies, getTopRatedMovies, IGetMovieResult } from '../api';
import Banner from '../Components/Common/Banner';
import Details from '../Components/Movie/Details';
import Slider from '../Components/Slider/Slider';

const Wrapper = styled.div`
    background-color: black;
    padding-bottom: 200px;
`;

const Loader = styled.div`
    height: 20vh;
    display: flex;
    justify-content: center;
    align-items: center;
`;

function Home(): JSX.Element {
    const location = useLocation();
    const movieMatch = matchPath('/movie/:category/:movieId', location.pathname);
    const homeMatch = matchPath('/', location.pathname);


    const { data: topRatedMovies, isLoading: isTopRatedLoading } = useQuery<IGetMovieResult>(
        ['movie', 'topRated'],
        getTopRatedMovies,
        { enabled: (movieMatch !== null) || (homeMatch !== null) }
    );

    const { data: latestMovies, isLoading: isLatestLoading } = useQuery<IGetMovieResult>(
        ['movie', 'latest'],
        getLatestMovies,
        { enabled: (movieMatch !== null) || (homeMatch !== null) }
    );

    const { data: upComingMovies, isLoading: isUpComingLoading } = useQuery<IGetMovieResult>(
        ['movie', 'upComing'],
        getUpComingMovies,
        { enabled: (movieMatch !== null) || (homeMatch !== null) }
    );

    const { data: nowPlayingMovies, isLoading: isNowPlayingLoading } = useQuery<IGetMovieResult>(
        ['movie', 'nowPlaying'],
        getNowPayingMovies,
        { enabled: (movieMatch !== null) || (homeMatch !== null) }
    );

    const firstMovie = nowPlayingMovies?.results[0];

    const isLoading = isTopRatedLoading || isLatestLoading || isUpComingLoading || isNowPlayingLoading;

    const clickedMovie = () => {
        if (!movieMatch?.params.movieId) {
            return;
        }

        if (movieMatch?.params.category === "Now Playing") {
            return nowPlayingMovies?.results.find(movie => movie.id + "" === movieMatch.params.movieId);
        }

        if (movieMatch?.params.category === "Up Coming") {
            return upComingMovies?.results.find(movie => movie.id + "" === movieMatch.params.movieId);
        }

        if (movieMatch?.params.category === "Latest Movies") {
            return latestMovies?.results.find(movie => movie.id + "" === movieMatch.params.movieId);
        }

        if (movieMatch?.params.category === "TopRated Movies") {
            return topRatedMovies?.results.find(movie => movie.id + "" === movieMatch.params.movieId);
        }
    }

    const movie = clickedMovie();



    return (
        <Wrapper>
            {isLoading ? (
                <Loader>Loading...</Loader>
            ) : (
                <>
                    <Banner imagePath={firstMovie?.backdrop_path!} title={firstMovie?.title!} overview={firstMovie?.overview!} />
                    <Slider menu="movie" tvs={undefined} movies={nowPlayingMovies} category="Now Playing"></Slider>
                    <Slider menu="movie" tvs={undefined} movies={upComingMovies} category="Up Coming"></Slider>
                    {/* <Slider menu="movie" tvs={undefined} movies={latestMovies} category="Latest Movies"></Slider> */}
                    <Slider menu="movie" tvs={undefined} movies={topRatedMovies} category="TopRated Movies"></Slider>
                    {movieMatch && <Details menu="movie" title={movie?.title!} imagePath={movie?.backdrop_path!} overview={movie?.overview!} />}
                </>
            )}
        </Wrapper>
    );
}

export default Home;