import { motion } from "framer-motion";
import { ReactNode, useState } from "react";
import styled from "styled-components";


interface ISliderRowProps {
    key: number;
    children: ReactNode;
}


const Row = styled(motion.div)`
    margin-top: 10px;
    margin-bottom: 150px;
    padding-top: 10px;
    gap: 5px;
    display: grid;
    grid-template-columns: repeat(6, 1fr);
    position: relative;
    width: 90vw;
`;

const rowVariants = {
    hidden: {
        x: window.outerWidth + 5,
    },
    visible: {
        x: 0,
    },
    exit: {
        x: -window.outerWidth - 5,
    },
};


function SliderRow({ key, children }: ISliderRowProps) {
    return (
        <Row
            variants={rowVariants}
            initial="hidden"
            animate="visible"
            exit="exit"
            key={key}
        >
            {children}
        </Row>
    );
}

export default SliderRow;