import { AnimatePresence, motion } from "framer-motion";
import { useState } from "react";
import styled from "styled-components";
import { IGetMovieResult, IGetTVResult } from "../../api";
import { slicePage } from "../../utils";
import SliderItem from "./SliderItem";
import SliderRow from "./SliderRow";

interface ISliderProps {
    category: string;
    movies: IGetMovieResult | undefined;
    tvs: IGetTVResult | undefined;
    menu: string;
}

const Wrapper = styled.div`
    position: relative;
    top: -150px;
    margin-left: 5vw;
    margin-right: 5vw;
`;

const Title = styled.h2`
    text-align: left;
    padding-top: 10px;
    margin-top: -50px;
    font-size:28px;
    position: absolute;
    color: ${(props) => props.theme.white.lighter};
`


function Slider({ menu, movies, tvs, category }: ISliderProps) {
    const [leaving, setLeaving] = useState(false);
    const toggleLeaving = (): void => setLeaving((prev) => !prev);
    const offset = 6;

    const [index, setIndex] = useState(0);

    const data = movies ? movies : tvs;

    const increaseIndex = (): void => {
        if (data) {
            const totalMovies = data.results.length - 1;
            const maxIndex = Math.floor(totalMovies / offset) - 1;

            if (leaving) {
                return;
            }
            toggleLeaving();

            setIndex((oldIndex): number => {
                if (oldIndex === maxIndex) {
                    return 0;
                }
                return oldIndex + 1;
            });
        }
    };

    return (
        <Wrapper>
            <Title> {category} </Title>
            <AnimatePresence
                initial={false}
                onExitComplete={toggleLeaving}
            >
                <SliderRow key={index}>
                    {
                        movies ? slicePage(
                            movies?.results.slice(1),
                            offset,
                            index
                        ).map((m) => (
                            <SliderItem menu={menu} category={category} id={m.id} title={m.title} imagePath={m.backdrop_path} />
                        )) : slicePage(
                            tvs?.results.slice(1),
                            offset,
                            index
                        ).map((m) => (
                            <SliderItem menu={menu} category={category} id={m.id} title={m.name} imagePath={m.backdrop_path} />
                        ))
                    }
                </SliderRow>
            </AnimatePresence>
        </Wrapper>
    )
}

export default Slider;